import React, { useState, useRef, useEffect } from 'react';
import {
  View,
  Text,
  StyleSheet,
  TouchableOpacity,
  StatusBar,
  SafeAreaView,
  Animated,
  KeyboardAvoidingView,
  Platform,
  ScrollView,
  Alert,
} from 'react-native';
import { useNavigation } from '@react-navigation/native';
import { NativeStackNavigationProp } from '@react-navigation/native-stack';
import { useDispatch, useSelector } from 'react-redux';
import { AuthStackParamList } from '../../types/navigation';
import { RootState } from '../../types';
import { colors, spacing, fonts, borderRadius, shadows } from '../../constants/theme';
import { AuthInput } from '../../components/common/AuthInput';
import { AuthButton } from '../../components/common/AuthButton';
import { LoadingScreen } from '../../components/common/LoadingScreen';
import { authService } from '../../services/authService';

type RegisterScreenNavigationProp = NativeStackNavigationProp<
  AuthStackParamList,
  'Register'
>;

interface FormData {
  username: string;
  email: string;
  password: string;
  confirmPassword: string;
  phone?: string;
}

interface FormErrors {
  username?: string;
  email?: string;
  password?: string;
  confirmPassword?: string;
  phone?: string;
  general?: string;
}

export const RegisterScreen: React.FC = () => {
  const navigation = useNavigation<RegisterScreenNavigationProp>();
  const dispatch = useDispatch();
  const { loading } = useSelector((state: RootState) => state.auth);

  const [formData, setFormData] = useState<FormData>({
    username: '',
    email: '',
    password: '',
    confirmPassword: '',
    phone: '',
  });
  const [errors, setErrors] = useState<FormErrors>({});
  const [showPassword, setShowPassword] = useState(false);
  const [showConfirmPassword, setShowConfirmPassword] = useState(false);
  const [agreeToTerms, setAgreeToTerms] = useState(false);
  const [isLoading, setIsLoading] = useState(false);

  // Âä®ÁîªÂÄº
  const fadeAnim = useRef(new Animated.Value(0)).current;
  const slideAnim = useRef(new Animated.Value(30)).current;
  const shakeAnim = useRef(new Animated.Value(0)).current;

  useEffect(() => {
    // ÂêØÂä®ÂÖ•Âú∫Âä®Áîª
    Animated.parallel([
      Animated.timing(fadeAnim, {
        toValue: 1,
        duration: 800,
        useNativeDriver: true,
      }),
      Animated.timing(slideAnim, {
        toValue: 0,
        duration: 600,
        useNativeDriver: true,
      }),
    ]).start();
  }, []);

  // Ë°®ÂçïÈ™åËØÅ
  const validateForm = (): boolean => {
    const newErrors: FormErrors = {};

    // Áî®Êà∑ÂêçÈ™åËØÅ
    if (!formData.username.trim()) {
      newErrors.username = 'ËØ∑ËæìÂÖ•Áî®Êà∑Âêç';
    } else if (formData.username.length < 2) {
      newErrors.username = 'Áî®Êà∑ÂêçËá≥Â∞ëÈúÄË¶Å2‰∏™Â≠óÁ¨¶';
    } else if (formData.username.length > 20) {
      newErrors.username = 'Áî®Êà∑Âêç‰∏çËÉΩË∂ÖËøá20‰∏™Â≠óÁ¨¶';
    } else if (!/^[a-zA-Z0-9\u4e00-\u9fa5_]+$/.test(formData.username)) {
      newErrors.username = 'Áî®Êà∑ÂêçÂè™ËÉΩÂåÖÂê´Â≠óÊØç„ÄÅÊï∞Â≠ó„ÄÅ‰∏≠ÊñáÂíå‰∏ãÂàíÁ∫ø';
    }

    // ÈÇÆÁÆ±È™åËØÅ
    if (!formData.email.trim()) {
      newErrors.email = 'ËØ∑ËæìÂÖ•ÈÇÆÁÆ±Âú∞ÂùÄ';
    } else if (!/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(formData.email)) {
      newErrors.email = 'ËØ∑ËæìÂÖ•ÊúâÊïàÁöÑÈÇÆÁÆ±Âú∞ÂùÄ';
    }

    // ÂØÜÁ†ÅÈ™åËØÅ
    if (!formData.password) {
      newErrors.password = 'ËØ∑ËæìÂÖ•ÂØÜÁ†Å';
    } else if (formData.password.length < 8) {
      newErrors.password = 'ÂØÜÁ†ÅËá≥Â∞ëÈúÄË¶Å8‰ΩçÂ≠óÁ¨¶';
    } else if (!/(?=.*[a-z])(?=.*[A-Z])(?=.*\d)/.test(formData.password)) {
      newErrors.password = 'ÂØÜÁ†ÅÈúÄË¶ÅÂåÖÂê´Â§ßÂ∞èÂÜôÂ≠óÊØçÂíåÊï∞Â≠ó';
    }

    // Á°ÆËÆ§ÂØÜÁ†ÅÈ™åËØÅ
    if (!formData.confirmPassword) {
      newErrors.confirmPassword = 'ËØ∑Á°ÆËÆ§ÂØÜÁ†Å';
    } else if (formData.password !== formData.confirmPassword) {
      newErrors.confirmPassword = '‰∏§Ê¨°ËæìÂÖ•ÁöÑÂØÜÁ†Å‰∏ç‰∏ÄËá¥';
    }

    // ÊâãÊú∫Âè∑È™åËØÅÔºàÂèØÈÄâÔºâ
    if (formData.phone && formData.phone.trim()) {
      if (!/^1[3-9]\d{9}$/.test(formData.phone)) {
        newErrors.phone = 'ËØ∑ËæìÂÖ•ÊúâÊïàÁöÑÊâãÊú∫Âè∑Á†Å';
      }
    }

    // Áî®Êà∑ÂçèËÆÆÈ™åËØÅ
    if (!agreeToTerms) {
      newErrors.general = 'ËØ∑ÈòÖËØªÂπ∂ÂêåÊÑèÁî®Êà∑ÂçèËÆÆÂíåÈöêÁßÅÊîøÁ≠ñ';
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  // ÈúáÂä®Âä®Áîª
  const triggerShakeAnimation = () => {
    Animated.sequence([
      Animated.timing(shakeAnim, {
        toValue: 10,
        duration: 100,
        useNativeDriver: true,
      }),
      Animated.timing(shakeAnim, {
        toValue: -10,
        duration: 100,
        useNativeDriver: true,
      }),
      Animated.timing(shakeAnim, {
        toValue: 10,
        duration: 100,
        useNativeDriver: true,
      }),
      Animated.timing(shakeAnim, {
        toValue: 0,
        duration: 100,
        useNativeDriver: true,
      }),
    ]).start();
  };

  // Â§ÑÁêÜÊ≥®ÂÜå
  const handleRegister = async () => {
    if (!validateForm()) {
      triggerShakeAnimation();
      return;
    }

    setIsLoading(true);
    setErrors({});

    try {
      const response = await authService.register({
        username: formData.username.trim(),
        email: formData.email.trim(),
        password: formData.password,
        phone: formData.phone?.trim() || undefined,
      });

      // Ê≥®ÂÜåÊàêÂäü
      console.log('Ê≥®ÂÜåÊàêÂäü:', response.user.username);
      Alert.alert(
        'Ê≥®ÂÜåÊàêÂäü',
        'Ê¨¢ËøéÂä†ÂÖ•Á¥¢ÂÖãÁîüÊ¥ªÔºÅ',
        [
          {
            text: 'ÂºÄÂßã‰ΩìÈ™å',
            onPress: () => {
              // ËøôÈáåÂ∫îËØ•ÈÄöËøáReduxÊõ¥Êñ∞ËÆ§ËØÅÁä∂ÊÄÅ
              // dispatch(registerSuccess(response));
            },
          },
        ]
      );
      
    } catch (error: any) {
      console.error('Ê≥®ÂÜåÂ§±Ë¥•:', error.message);
      setErrors({ general: error.message || 'Ê≥®ÂÜåÂ§±Ë¥•ÔºåËØ∑ÈáçËØï' });
      triggerShakeAnimation();
    } finally {
      setIsLoading(false);
    }
  };

  // Â§ÑÁêÜÁôªÂΩï
  const handleLogin = () => {
    navigation.navigate('Login');
  };

  // ËøîÂõûÊ¨¢ËøéÈ°µ
  const handleBack = () => {
    navigation.goBack();
  };

  // Êõ¥Êñ∞Ë°®ÂçïÊï∞ÊçÆ
  const updateFormData = (field: keyof FormData, value: string) => {
    setFormData(prev => ({ ...prev, [field]: value }));
    // Ê∏ÖÈô§ÂØπÂ∫îÂ≠óÊÆµÁöÑÈîôËØØ
    if (errors[field]) {
      setErrors(prev => ({ ...prev, [field]: undefined }));
    }
  };

  // Ê£ÄÊü•ÂØÜÁ†ÅÂº∫Â∫¶
  const getPasswordStrength = (password: string): { level: number; text: string; color: string } => {
    if (!password) return { level: 0, text: '', color: colors.textSecondary };
    
    let score = 0;
    if (password.length >= 8) score++;
    if (/[a-z]/.test(password)) score++;
    if (/[A-Z]/.test(password)) score++;
    if (/\d/.test(password)) score++;
    if (/[^a-zA-Z\d]/.test(password)) score++;

    if (score <= 2) return { level: 1, text: 'Âº±', color: colors.error };
    if (score <= 3) return { level: 2, text: '‰∏≠', color: colors.warning };
    return { level: 3, text: 'Âº∫', color: colors.success };
  };

  const passwordStrength = getPasswordStrength(formData.password);

  if (isLoading) {
    return <LoadingScreen message="Ê≠£Âú®Ê≥®ÂÜå..." />;
  }

  return (
    <SafeAreaView style={styles.container}>
      <StatusBar barStyle="dark-content" backgroundColor={colors.background} />
      
      <KeyboardAvoidingView
        style={styles.keyboardAvoid}
        behavior={Platform.OS === 'ios' ? 'padding' : 'height'}
      >
        <ScrollView
          style={styles.scrollView}
          contentContainerStyle={styles.scrollContent}
          showsVerticalScrollIndicator={false}
          keyboardShouldPersistTaps="handled"
        >
          {/* Â§¥ÈÉ®Âå∫Âüü */}
          <Animated.View
            style={[
              styles.header,
              {
                opacity: fadeAnim,
                transform: [{ translateY: slideAnim }],
              },
            ]}
          >
            <TouchableOpacity style={styles.backButton} onPress={handleBack}>
              <Text style={styles.backButtonText}>‚Üê</Text>
            </TouchableOpacity>
            
            <View style={styles.titleContainer}>
              <Text style={styles.title}>ÂàõÂª∫Ë¥¶Êà∑</Text>
              <Text style={styles.subtitle}>Âä†ÂÖ•Á¥¢ÂÖãÁîüÊ¥ªÔºåÂºÄÂêØÂÅ•Â∫∑‰πãÊóÖ</Text>
            </View>
          </Animated.View>

          {/* Ë°®ÂçïÂå∫Âüü */}
          <Animated.View
            style={[
              styles.formContainer,
              {
                opacity: fadeAnim,
                transform: [
                  { translateY: slideAnim },
                  { translateX: shakeAnim },
                ],
              },
            ]}
          >
            {/* ÈîôËØØÊèêÁ§∫ */}
            {errors.general && (
              <View style={styles.errorContainer}>
                <Text style={styles.errorText}>{errors.general}</Text>
              </View>
            )}

            {/* Áî®Êà∑ÂêçËæìÂÖ• */}
            <AuthInput
              label="Áî®Êà∑Âêç"
              placeholder="ËØ∑ËæìÂÖ•Áî®Êà∑Âêç"
              value={formData.username}
              onChangeText={(value) => updateFormData('username', value)}
              error={errors.username}
              autoCapitalize="none"
              icon="üë§"
              maxLength={20}
              counter
            />

            {/* ÈÇÆÁÆ±ËæìÂÖ• */}
            <AuthInput
              label="ÈÇÆÁÆ±Âú∞ÂùÄ"
              placeholder="ËØ∑ËæìÂÖ•ÊÇ®ÁöÑÈÇÆÁÆ±"
              value={formData.email}
              onChangeText={(value) => updateFormData('email', value)}
              error={errors.email}
              keyboardType="email-address"
              autoCapitalize="none"
              autoComplete="email"
              icon="üìß"
            />

            {/* ÊâãÊú∫Âè∑ËæìÂÖ•ÔºàÂèØÈÄâÔºâ */}
            <AuthInput
              label="ÊâãÊú∫Âè∑Á†ÅÔºàÂèØÈÄâÔºâ"
              placeholder="ËØ∑ËæìÂÖ•ÊÇ®ÁöÑÊâãÊú∫Âè∑"
              value={formData.phone}
              onChangeText={(value) => updateFormData('phone', value)}
              error={errors.phone}
              keyboardType="phone-pad"
              icon="üì±"
              maxLength={11}
            />

            {/* ÂØÜÁ†ÅËæìÂÖ• */}
            <View style={styles.passwordContainer}>
              <AuthInput
                label="ÂØÜÁ†Å"
                placeholder="ËØ∑ËæìÂÖ•ÂØÜÁ†Å"
                value={formData.password}
                onChangeText={(value) => updateFormData('password', value)}
                error={errors.password}
                secureTextEntry={!showPassword}
                icon="üîí"
                rightIcon={showPassword ? "üëÅÔ∏è" : "üëÅÔ∏è‚Äçüó®Ô∏è"}
                onRightIconPress={() => setShowPassword(!showPassword)}
              />
              {/* ÂØÜÁ†ÅÂº∫Â∫¶ÊåáÁ§∫Âô® */}
              {formData.password && (
                <View style={styles.passwordStrengthContainer}>
                  <Text style={styles.passwordStrengthLabel}>ÂØÜÁ†ÅÂº∫Â∫¶Ôºö</Text>
                  <View style={styles.passwordStrengthBar}>
                    <View
                      style={[
                        styles.passwordStrengthFill,
                        {
                          width: `${(passwordStrength.level / 3) * 100}%`,
                          backgroundColor: passwordStrength.color,
                        },
                      ]}
                    />
                  </View>
                  <Text style={[styles.passwordStrengthText, { color: passwordStrength.color }]}>
                    {passwordStrength.text}
                  </Text>
                </View>
              )}
            </View>

            {/* Á°ÆËÆ§ÂØÜÁ†ÅËæìÂÖ• */}
            <AuthInput
              label="Á°ÆËÆ§ÂØÜÁ†Å"
              placeholder="ËØ∑ÂÜçÊ¨°ËæìÂÖ•ÂØÜÁ†Å"
              value={formData.confirmPassword}
              onChangeText={(value) => updateFormData('confirmPassword', value)}
              error={errors.confirmPassword}
              secureTextEntry={!showConfirmPassword}
              icon="üîí"
              rightIcon={showConfirmPassword ? "üëÅÔ∏è" : "üëÅÔ∏è‚Äçüó®Ô∏è"}
              onRightIconPress={() => setShowConfirmPassword(!showConfirmPassword)}
            />

            {/* Áî®Êà∑ÂçèËÆÆ */}
            <View style={styles.termsContainer}>
              <TouchableOpacity
                style={styles.checkboxContainer}
                onPress={() => setAgreeToTerms(!agreeToTerms)}
              >
                <View style={[styles.checkbox, agreeToTerms && styles.checkboxChecked]}>
                  {agreeToTerms && <Text style={styles.checkmark}>‚úì</Text>}
                </View>
                <Text style={styles.termsText}>
                  ÊàëÂ∑≤ÈòÖËØªÂπ∂ÂêåÊÑè{' '}
                  <Text style={styles.linkText}>„ÄäÁî®Êà∑ÂçèËÆÆ„Äã</Text>
                  {' '}Âíå{' '}
                  <Text style={styles.linkText}>„ÄäÈöêÁßÅÊîøÁ≠ñ„Äã</Text>
                </Text>
              </TouchableOpacity>
            </View>

            {/* Ê≥®ÂÜåÊåâÈíÆ */}
            <AuthButton
              title="ÂàõÂª∫Ë¥¶Êà∑"
              onPress={handleRegister}
              loading={isLoading}
              style={styles.registerButton}
            />

            {/* ÂàÜÂâ≤Á∫ø */}
            <View style={styles.dividerContainer}>
              <View style={styles.divider} />
              <Text style={styles.dividerText}>Êàñ</Text>
              <View style={styles.divider} />
            </View>

            {/* Á¨¨‰∏âÊñπÊ≥®ÂÜå */}
            <View style={styles.socialLoginContainer}>
              <TouchableOpacity style={styles.socialButton}>
                <Text style={styles.socialButtonText}>üçé AppleÊ≥®ÂÜå</Text>
              </TouchableOpacity>
              
              <TouchableOpacity style={styles.socialButton}>
                <Text style={styles.socialButtonText}>üì± ÂæÆ‰ø°Ê≥®ÂÜå</Text>
              </TouchableOpacity>
            </View>
          </Animated.View>

          {/* Â∫ïÈÉ®ÁôªÂΩïÈìæÊé• */}
          <Animated.View
            style={[
              styles.footer,
              {
                opacity: fadeAnim,
                transform: [{ translateY: slideAnim }],
              },
            ]}
          >
            <Text style={styles.footerText}>
              Â∑≤ÊúâË¥¶Êà∑Ôºü{' '}
              <TouchableOpacity onPress={handleLogin}>
                <Text style={styles.loginLink}>Á´ãÂç≥ÁôªÂΩï</Text>
              </TouchableOpacity>
            </Text>
          </Animated.View>
        </ScrollView>
      </KeyboardAvoidingView>
    </SafeAreaView>
  );
};

